import './style/style.scss'
import pic from './style/img/play.svg'


const HomePreview = () => {

    let hidd = () =>{
        document.getElementById('modal__video').classList.toggle('disable')
        document.body.classList.toggle('body_noscroll')
    }


  

    return (
        <>
            <article className="home_preview">

                <section className="preview">
                    <h1>we do the desing of any complexity</h1>
                    <div className="preview_desc">
                        It is a long established fact that a reader will
                        be distracted by the readable content of a page
                        when looking at its layout. The point of using
                        Lorem Ipsum is that it has a more-or-less normal
                        distribution of letters, as opposed to using
                        'Content here, content here', making it look
                </div>
                    <div className="preview_butt">
                        <button onClick={hidd} >
                            <svg className="preview_butt_img" xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24">
                                <path d="M0 0h24v24H0z" fill="none" />
                                <path d="M10 16.5l6-4.5-6-4.5v9zM12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z" />
                            </svg>
                        watch video
                    </button>
                    </div>

                </section>


            </article>
        </>

    )
}
export default HomePreview;